@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
:root{
  /****************BODY FONT****************/
  --bodyFont: "Ubuntu";

  /****************COLOR****************/
  --primary-white:#fefefe;
  --primary-blue: #2e79c8;
  --primary-brown: #68303e;
  --primary-orange: #ff834f;
  --primary-black: #010103;
  --primary-grey: #282c34;
  --primary-green: #5FA423;
}

/****test footer****/
.footers {
  display: flex;
  
  flex-direction: row;
  align-items: flex-end;
  padding: 1em;
  background: url('data:image/svg+xml;base64,');
  background-repeat: repeat-x;
  background-size: cover;
  color: white;
}

.footers div {
  flex-grow: 1;
}


/****************NAVTOP****************/
.contact {
  color: red !important;
  font-size: 18px;
}

/****************NAVBAR****************/
.logo{
  width: 100px;
  height: 100px;
}

nav a.nav-link{
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}
nav ul li{
  text-transform: uppercase;
}
nav ul{
  margin-left: auto !important;    
}

.navbar-nav > li > a{
  font-size: 1rem;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #282c34 !important;
  color: var(--primary-grey) !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus{
  border-bottom: 0.1875rem solid #68303e;
  border-bottom: 0.1875rem solid var(--primary-brown);

}

.navbar-brand{
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

.navbar-nav > li > a:hover{
  border-bottom: 0.1875rem solid #010103;
  border-bottom: 0.1875rem solid var(--primary-black);
}

.menu{
  background-color: #ff834f;
  background-color: var(--primary-orange);
}


.BtnBookNow{
  background-color: #282c34 !important;
  background-color: var(--primary-grey) !important;
  color: #fefefe !important;
  color: var(--primary-white) !important;
}


/****************NAVTOP****************/
.navtop{
  background-color: #282c34;
  background-color: var(--primary-grey);
}

.social > a{
  color: #fefefe !important;
  color: var(--primary-white) !important;
}

.social{
  text-align: right;
}

.contact > a{
  color: #fefefe !important;
  color: var(--primary-white) !important;
}

.contact> a:hover{
  color: #ff834f !important;
  color: var(--primary-orange) !important;
}

.social> a:hover{
  color: #ff834f !important;
  color: var(--primary-orange) !important;
}
/****************SLIDER****************/



html, body {
  max-width: 100%;
  overflow-x: hidden;
}


/********************FOOTER********************/
.footer{
  background-color: #282c34;
  background-color: var(--primary-grey);
  color: #fefefe !important;
  color: var(--primary-white) !important;  
  
}

.contact-footer{
  padding-bottom: 20px;
  -webkit-text-emphasis: none;
          text-emphasis: none;
}

.logofooter{
  height: 100px;
  width: 100px;
  
}

.description-footer{
  text-align: justify;
  align-items: flex-end;
}

.contact-list > a {
  color: #fefefe;
  color: var(--primary-white);
  text-decoration: none !important;
  cursor: pointer !important;
}

.contact-list > a:hover{
  color: #ff834f;
  color: var(--primary-orange);
}

.social-footer > a{
  color: #fefefe;
  color: var(--primary-white);
  cursor: pointer !important;
}

.social-footer > a:hover{
  color: #ff834f;
  color: var(--primary-orange);
}


.legacy > a{
  color: #fefefe !important;
  color: var(--primary-white) !important;
}

.legacy > a:hover{
  color: #ff834f !important;
  color: var(--primary-orange) !important;
}

.copyright{
  background-color: #ff834f;
  background-color: var(--primary-orange);
  color: #282c34;
  color: var(--primary-grey);
}

.copyrightText> a{
  color: #282c34;
  color: var(--primary-grey);
}

.copyrightText> a:hover{
  color: #68303e;
  color: var(--primary-brown);
}

.subscribe{
  background-color: #ff834f !important;
  background-color: var(--primary-orange) !important;
  color: #282c34 !important;
  color: var(--primary-grey) !important;
  text-transform:none !important;
  margin-bottom: 4px !important;
  text-decoration: none !important;
}

.mailInput{
  height: 40px;
  margin-top: 2px;
  border-radius: 5px;
  padding: 10px !important;
}
