#AproposSection1>h4{   
  text-align: center;   
}
#AproposSection1>p{   
  text-align: justify;    
}

/*Commentaire test branch*/
h3{
  -webkit-text-emphasis: none;
          text-emphasis: none;
}

/*******************TEAM****************/


.img1{
  width: 200px;
  height: 200px;
  
}
.team-photo{
  justify-content: center;
  display: block !important;
  margin-left: auto;
margin-right: auto;
}


.team-section{
  color: #282c34 !important;
  color: var(--primary-grey) !important;
  background-repeat: no-repeat;
  border-radius: 10px;
  
}

/* #bg-conf{
  background-image: url("../images/qerozia-photo.png") !important;
  background-repeat: repeat;
  padding-top: 20px;
  padding-bottom: 20px;
} */

.card-team-each{
  background-color: transparent;
  color: #ff834f;
  color: var(--primary-orange);
  padding-top: 3px;
  padding-bottom: 3px;
  box-shadow: 0 4px 8px 0 #282c34, 0 6px 20px 0 #282c34 !important;
  box-shadow: 0 4px 8px 0 var(--primary-grey), 0 6px 20px 0 var(--primary-grey) !important;
  text-align: center;
}

.espacement{
  margin-right: 80px !important;
  margin-left: 80px !important;
}

.nom-team{
  border: 2px solid #ff834f !important;
  border: 2px solid var(--primary-orange) !important;
  padding-top: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
  vertical-align: middle;
  background-color: #ff834f;
  background-color: var(--primary-orange);
  color: #282c34;
  color: var(--primary-grey);
  text-align: center;
}

.social-icon{
  color: #fefefe !important;
  color: var(--primary-white) !important;
}

.social-icon:hover{
  color: #ff834f !important;
  color: var(--primary-orange) !important;
}

.nom-poste{
  color: #ff834f !important;
  color: var(--primary-orange) !important;
}

.card-body{
  justify-content: center !important;
}

.card-text{
  color: #ff834f;
  color: var(--primary-orange);
}

.card-group{
  background-color: #282c34 !important;
  background-color: var(--primary-grey) !important;
}

/*******************TOUR****************/

.bouton-expertise{
  background-color: #ff834f !important;
  background-color: var(--primary-orange) !important;
  color: #282c34 !important;
  color: var(--primary-grey) !important;
  border-radius: 5px !important;
  border-color: #282c34 !important;
  border-color: var(--primary-grey) !important;
  height: 45px !important;
  width: 48%;
  
}

.bouton-expertise:hover{
  background-color: #fefefe !important;
  background-color: var(--primary-white) !important;
  color: var(--chocolat) !important;
  
}

.bouton-expertise >a {
  text-decoration: none !important;
  cursor: pointer;
  color: inherit !important;

}

.card-title{
  color: #fefefe !important;
  color: var(--primary-white) !important;
}
/****************CARD**********************/
.card{
  background-color: #282c34 !important;
  background-color: var(--primary-grey) !important;
}
.card-icons svg{
  display: block;
  margin: auto;
  font-size: 50px;
  color: var(--chocolat);

}

.selectpicker{
  background-color: transparent !important;
  display: inline-block;
  border: none;
  color: var(--primary-background);
}

.selectpicker svg {
  color: var(--primary-background);
}

.droite-btn{
  float: right;
  background-color: #fefefe !important;
  background-color: var(--primary-white) !important;
}

.droite-btn:hover{
  background-color: #ff834f !important;
  background-color: var(--primary-orange) !important;
}


